import React from 'react';

const DisclaimerPopup = ({ onAccept }) => {
  return (
    <div className="fixed top-0 left-0 w-full h-full flex items-center justify-center backdrop-blur-md z-20">
      <div className="bg-white p-4 rounded-lg shadow-lg max-w-3xl text-lg">
        <h2 className="text-xl font-bold mb-2">Disclaimer</h2>
        <p className="text-gray-700">
        This is a live demo version of the frontend only, and certain features are limited. <br/>
        Please note that lesson containers will not provision, meaning you won't be able to access practice terminals or complete lessons.
        <br/>
        Additionally, please be aware that this is an in-development frontend, and not every page is available yet.
        <br/>
        The two courses available in this demo are <span className='text-blue-400'>Operating Systems</span> and <span className='text-blue-400'>Python</span>.
        <br/><br/>
        If you have any questions or need assistance, feel free to contact me at hunterlauder@gmail.com
        <br/><br/>
        Thank you for using my demo!
        </p>
        <div className="mt-4 w-full flex justify-center">
            <button className='bg-blue-400 hover:bg-blue-500 w-auto duration-200 text-white px-6 py-2 rounded-3xl'
            onClick={onAccept}>Accept</button>
        </div>
      </div>
    </div>
  );
}

export { DisclaimerPopup as default}